"use client";

import { useAnalytics } from "@/hooks/useAnalytics";
import { BarChart2, CheckSquare, Book, Flame, Activity } from "lucide-react";

export default function AnalyticsPage() {
  const { stats, loading } = useAnalytics();

  if (loading) {
    return <div className="p-8">Loading analytics...</div>;
  }

  return (
    <div className="max-w-6xl">
      <h1 className="mb-8 text-3xl font-bold text-gray-800">Analytics</h1>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
        <div className="rounded-lg bg-white p-6 shadow-md">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="text-lg font-semibold text-gray-700">Tasks</h3>
            <CheckSquare className="h-6 w-6 text-blue-500" />
          </div>
          <p className="text-3xl font-bold text-gray-800">
            {stats.completedTodos}
          </p>
          <p className="text-sm text-gray-500">Completed Tasks</p>
          <div className="mt-4 h-2 w-full rounded-full bg-gray-100">
            <div
              className="h-2 rounded-full bg-blue-500"
              style={{
                width: `${
                  (stats.completedTodos /
                    (stats.completedTodos + stats.activeTodos || 1)) *
                  100
                }%`,
              }}
            ></div>
          </div>
          <p className="mt-2 text-xs text-gray-400">
            {stats.activeTodos} tasks remaining
          </p>
        </div>

        <div className="rounded-lg bg-white p-6 shadow-md">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="text-lg font-semibold text-gray-700">Journal</h3>
            <Book className="h-6 w-6 text-purple-500" />
          </div>
          <p className="text-3xl font-bold text-gray-800">
            {stats.totalJournalEntries}
          </p>
          <p className="text-sm text-gray-500">Total Entries</p>
        </div>

        <div className="rounded-lg bg-white p-6 shadow-md">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="text-lg font-semibold text-gray-700">Habits</h3>
            <Activity className="h-6 w-6 text-green-500" />
          </div>
          <p className="text-3xl font-bold text-gray-800">
            {stats.activeHabits}
          </p>
          <p className="text-sm text-gray-500">Active Habits</p>
        </div>

        <div className="rounded-lg bg-white p-6 shadow-md">
          <div className="mb-4 flex items-center justify-between">
            <h3 className="text-lg font-semibold text-gray-700">Streaks</h3>
            <Flame className="h-6 w-6 text-orange-500" />
          </div>
          <p className="text-3xl font-bold text-gray-800">
            {stats.habitStreaks}
          </p>
          <p className="text-sm text-gray-500">Total Streak Days</p>
        </div>
      </div>

      <div className="mt-8 rounded-lg bg-white p-6 shadow-md">
        <h3 className="mb-4 text-xl font-semibold text-gray-800">
          Productivity Overview
        </h3>
        <div className="flex h-64 items-center justify-center rounded-lg bg-gray-50 border-2 border-dashed border-gray-200">
          <p className="text-gray-400">Chart visualization coming soon...</p>
        </div>
      </div>
    </div>
  );
}
